div.seomatic-field {
  border: 1px solid #e3e5e8;
  margin-bottom: 10px;
  padding: 0;
  border-radius: 3px;
  background-color: rgb(235, 237, 239);
}

div.seomatic-tab-content {
  -webkit-box-flex: 1;
  flex: 1;
  background: #fff;
  padding: 24px;
  word-wrap: break-word;
}

nav.seomatic-tabs {
  position: relative;
  z-index: 1;
  box-shadow: inset 0 -1px 0 #e3e5e8;
  min-height: 40px;
  overflow: hidden;
}

nav.seomatic-tabs ul {
  display: -webkit-box;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  flex-direction: row;
  width: calc(100% + 1px);
}

nav.seomatic-tabs ul li {
  box-sizing: border-box;
  border-right: 1px solid rgba(0, 0, 20, 0.1);
}

nav.seomatic-tabs ul li a {
  position: relative;
  display: block;
  padding: 10px 24px;
  white-space: nowrap;
  overflow: hidden;
  color: #576575;
  text-decoration: none;
}

nav.seomatic-tabs ul li a:active, nav.seomatic-tabs ul li a:focus {
  outline: 0;
  border: none;
  -moz-outline-style: none;
}

nav.seomatic-tabs ul li a.sel {
  color: #29323d;
  background: #fff;
  padding-bottom: 10px;
  cursor: default;
}

nav.seomatic-tabs ul li a:not(.sel):hover {
  color: #0d78f2;
}

nav.seomatic-tabs ul li a:after {
  background: linear-gradient(to right, rgba(235, 237, 239, 0), #ebedef 17px);
}

nav.seomatic-tabs ul li a.sel:after {
  background: linear-gradient(to right, rgba(255, 255, 255, 0), #fff 17px);
}

div.tokenfield {
  border: 1px solid #e3e5e8;
  border-radius: 3px;
  padding: 5px 7px;
  box-sizing: border-box;
}


span.inline-item,
div.inline-item {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
}

.inline-item > div,
.inline-item > div > label {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
}

div.half-width-field {
