}

div.preview-column {
  float: none;
  width: 100%;
}

div.displaypreview {
  width: 512px;
  overflow: hidden;
  border: 1px solid #EEE;
  padding: 30px;
}

ul.checkboxChecklist {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

li.checkboxChecklist {
  position: relative;
  list-style-type: none!important;
  color: #333;
}

li.checkboxChecklist.checked {
  color: #008002;
}

li.checkboxChecklist label {
  position: absolute;
  width: 14px;
  height: 14px;
  top: 3px;
  left: -22px;
  background: transparent;
  border:1px solid;
}

li.checkboxChecklist a {
  color: inherit;
}

li.checkboxChecklist.checked label:after {
  content: '';
  position: absolute;
  width: 9px;
  height: 5px;
  background: transparent;
  top: 2px;
  left: 2px;
  border: 2px solid;
  border-top: none;
  border-right: none;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}

div.vue-treeselect > div.vue-treeselect__control {
  box-shadow: inset 0 1px 4px -1px rgba(96, 125, 159, 0.25);
  background-color: #fbfcfe;
}

